import React from 'react';
import message from 'shared/lib/message';

const Test: React.FC = () => {
  const toast = () => {
    message.info('hello world 1');
    setTimeout(() => {
      message.info('hello world 2', undefined, () => {
        console.log('close 2');
      });
    }, 500);
    setTimeout(() => {
      message.info('hello world 3', undefined, () => {
        console.log('close 3');
      });
    }, 700);
  };

  const confirm = () => {
    message.confirm({
      shouldShowCancelButton: false,
      content: 'should you confirm',
      onConfirm: () => {
        showAnotherConfirm();
      },
      onCancel: () => {
        console.log('on cancel');
      },
    });
  };

  const showAnotherConfirm = () => {
    message.confirm({
      content: 'another confirm',
      onConfirm: () => {
        console.log('on another confirm');
      },
      onCancel: () => {
        console.log('on another cancel');
      },
    });
  };

  return (
    <>
      <button onClick={toast}>click toast</button>
      <button onClick={confirm}>click confirm</button>
    </>
  );
};

export default Test;
